import React, { useEffect, useState } from 'react';
import { PlusOutlined } from '@ant-design/icons';
import { message, Modal, Upload } from 'antd';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as BlogActions from '@/redux/actionCreator';
import InputImage from './InputImage';
const UploadImage: React.FC = (props: any) => {
  // é¢„è§ˆæ¨¡æ€æ¡†
  const [previewOpen, setPreviewOpen] = useState(false);
  // é¢„è§ˆå›¾ç‰‡
  const [previewImage, setPreviewImage] = useState('');
  // é¢„è§ˆå¤´éƒ¨
  const [previewTitle, setPreviewTitle] = useState('');
  // å›¾ç‰‡ä¿¡æ¯
  const [fileList, setFileList] = useState<any>([]);
  // åˆ é™¤åç½®ç©ºå›¾ç‰‡é“¾æ¥è¾“å…¥æ¡†
  const [imgUrl, setImgUrl] = useState({});
  // å›¾ç‰‡èµ‹å€¼
  useEffect(() => {
    setFileList(props.imgUrlArr !== undefined ? props.imgUrlArr : []);
  }, [props.imgUrlArr]);
  // ä¸Šä¼ ä¹‹å‰çš„æ“ä½œ
  const beforeUpload = (file: any) => {
    // ä¸Šä¼ å›¾ç‰‡æ ¼å¼
    const isJpgOrPng =
      file.type === 'image/jpeg' ||
      file.type === 'image/png' ||
      file.type === 'image/webp' ||
      file.type === 'image/jpg' ||
      file.type === 'image/gif';
    if (!isJpgOrPng) {
      message.error('åªå…è®¸ä¸Šä¼  JPG/PNG/WEBP/JPG/GIT æ–‡ä»¶!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
      message.error('å›¾ç‰‡å¤§å°å¿…é¡»å°äº 2MB!');
    }
    return isJpgOrPng && isLt2M;
  };
  // ä¸Šä¼ æ“ä½œ
  const uploadButton = (
    <div>
      <PlusOutlined />
      <div style={{ marginTop: 8 }}>Upload</div>
    </div>
  );
  // ä¸Šä¼ å›¾ç‰‡
  const handleUpload = (data: any) => {
    props.BlogActions.asyncFileUploadAction(data).then((result: any) => {
      message.success('ä¸Šä¼ æˆåŠŸ');
      if (result) {
        props.handleChange(result);
        setFileList([
          {
            name: result.name,
            thumbUrl: result.url,
          },
        ]);
      }
    });
  };

  // é¢„è§ˆ
  const handlePreview = (file: any) => {
    setPreviewImage(file.thumbUrl);
    setPreviewTitle(file.name);
    setPreviewOpen(true);
  };
  // å…³é—­é¢„è§ˆ
  const handleCancel = () => setPreviewOpen(false);
  // åˆ é™¤å›¾ç‰‡
  const hanleRemove = (it: any) => {
    props.handleRemove(it)
    setFileList([]);
    setImgUrl({
      url: '',
    });
  };
  // èµ‹å€¼ä¸Šä¼ å›¾ç‰‡
  const onChangeVal = (data: any) => {
    props.handleChange(data);
    // æˆªå–
    let start = data.indexOf('images');
    let end = data.indexOf('?', start);
    let name = data.substring(start, end);
    setFileList([
      {
        name: name,
        thumbUrl: data,
      },
    ]);
  };
  return (
    <>
      <Upload
        listType="picture-card"
        fileList={fileList}
        customRequest={handleUpload}
        onPreview={handlePreview}
        beforeUpload={beforeUpload}
        // onChange={handleChange}
        onRemove={hanleRemove}
      >
        {fileList.length >= 1 ? null : uploadButton}
      </Upload>
      {/* é¢„è§ˆ */}
      <Modal open={previewOpen} title={previewTitle} footer={null} onCancel={handleCancel}>
        <img alt="example" style={{ width: '100%' }} src={previewImage} />
      </Modal>
      <InputImage
        onChangeVal={onChangeVal}
        fileList={fileList ? fileList : [{ name: '', thumbUrl: '' }]}
        imgUrl={imgUrl}
      />
    </>
  );
};

const mapDispatchToProps = (dispatch: any) => {
  return {
    BlogActions: bindActionCreators(BlogActions, dispatch),
  };
};

export default connect(null, mapDispatchToProps)(UploadImage);
